class MyInternalItem extends React.PureComponent{
  render() {
    let id = this.props.item.id;
    return (
      <Card>
                    <TouchableOpacity  onPress={() => this.props.state.navigate('IssueScreen',{id: this.props.item.id,socket: "", title: this.props.item.title,showHeader: false,pageCount: this.props.item.link[3]['pse:count']})}>

          <CardItem cardBody button onPress={() => this.props.state.navigate('IssueScreen',{id: id,socket: this.props.state.socket,title: this.props.item.title,showHeader: false,pageCount: this.props.item.link[3]['pse:count']})}>
            <Image resizeMode={'contain'} source={{uri: "http://l2.mml2.net:2202" + this.props.item.link[0].href}} style={{height: 277,width:180,flex: 1}}/>
          </CardItem>
          </TouchableOpacity>

      </Card>
    )
  }
}
