export default class HomeScreen extends Component{

  state = {
    isLoading: true,
    isConnected: false,
    noStorage: true,
    books: [],
    data: [],
    search: ""

  };
  static navigationOptions = ({ navigation }) => {
    return {
        headerTitle: "Comic Library",
        headerShown: true,
        headerTitleStyle: {
            textAlign: 'center',
            alignSelf: 'center'
        },
        headerRight: (<View></View>)
    };
};
  componentDidMount(){
    const {navigate} = this.props.navigation;
    this.setState({
      navigate: navigate
    });
    const socket = io('http://l2.mml2.net:3000', {
      transports: ['websocket'],
    });
    this.setState({
      socket: socket
    });

    socket.on('connect', () => {
      this.setState({ isConnected: true });
    });

    socket.on('GotHome', data => {
      console.log("got books back");
     this.HandleHome(data);
    });
    this.GettingBooks();
    socket.emit("GetHome","mg");

  }

  renderHeader = () => {    
    return (      
      <SearchBar        
        placeholder="Type Here..."        
        lightTheme        
        round        
        onChangeText={text => this.searchFilterFunction(text)}
        autoCorrect={false}             
      />    
    );  
  };

  searchFilterFunction = search => {    
    this.setState({ search });
    const newData = this.state.books.filter(item => {      
      const itemData = `${item.title.toUpperCase()}   
      ${item.title.toUpperCase()} ${item.title.toUpperCase()}`;
      
       const textData = search.toUpperCase();
        
       return itemData.indexOf(textData) > -1;    
    }); 
    this.setState({ data: newData });  
  };

  updateSearch = search => {
    this.setState({ search });
  };
  GettingBooks = async () => {
    try {
      let books = await AsyncStorage.getItem('books') || [];
      this.setState({
        books: JSON.parse(books),
        noStorage: false,
        isLoading: false,
        data: JSON.parse(books)
      });
      
    } catch (e){
      console.log(e);
    }
  }
  componentWillUnmount(){
    console.log("trashing");
  }
  HandleHome = async(data) => {
    let books = [];
    for (var i=0;i<data.length;i++){
      var issueCount;
      let issues = JSON.parse(data[i].issues);
      issueCount = issues.length;
      if (!issueCount){
        issueCount = 0;
      }
      books.push({"title": data[i].title, "id": data[i].id,"issues":issues,"issueCount":issueCount});

    }
    this.setState({
      books: books,
      isLoading: false
    });
    try {
      await AsyncStorage.setItem('books',JSON.stringify(books));
    } catch (e){
      console.log(error);
    }
  }
  HandleIssue(data){
    let books = this.state.books;
    let obj = books.find(o => o.id === data.tempId.toString());
    for (var i=0;i<data.entry.length;i++){
      let issue = {"title":data.entry[i].title,"id":data.entry[i].id,"img": data.entry[i].link[0].href};
      obj.issues.push(issue);
    }
  }

  _renderItem = ({item, index}) => (
    <MyListItem item={item} state={this.state}/>
  );

  render() {
    const { search } = this.state;
    if (this.state.isLoading){
    return (
      <View style={styles.container}>
        <ScrollView
          style={styles.container}
          contentContainerStyle={styles.contentContainer}>
          <View style={styles.welcomeContainer}>
            <Image
              source={
                __DEV__
                  ? require('../assets/images/robot-dev.png')
                  : require('../assets/images/robot-prod.png')
              }
              style={styles.welcomeImage}
            />
          </View>

          <View style={styles.getStartedContainer}>
            <DevelopmentModeNotice />

            <Text style={styles.getStartedText}>Get started by opening2</Text>

            <View
              style={[styles.codeHighlightContainer, styles.homeScreenFilename]}>
              <MonoText>screens/HomeScreen.js</MonoText>
            </View>

            <Text style={styles.getStartedText}>
              Change this text and your app will automatically reload.
            </Text>
          </View>

          <View style={styles.helpContainer}>
            <TouchableOpacity onPress={handleHelpPress} style={styles.helpLink}>
              <Text style={styles.helpLinkText}>
                Help, it didnâ€™t automatically reload!
              </Text>
            </TouchableOpacity>
          </View>
        </ScrollView>

        <View style={styles.tabBarInfoContainer}>
          <Text style={styles.tabBarInfoText}>
            This is a tab bar. You can edit it in:
          </Text>

          <View
            style={[styles.codeHighlightContainer, styles.navigationFilename]}>
            <MonoText style={styles.codeHighlightText}>
              navigation/MainTabNavigator.js
            </MonoText>
          </View>
        </View>
      </View>
    );
            } else {
              return(
                <View style={styles.container}>
                              <SearchBar
        placeholder="Type Here..."
        onChangeText={this.searchFilterFunction}
        lightTheme={true}
        value={search}
      />
                  <FlatList style={styles.flatList}
                  data={this.state.data}
                  windowSize={4}
initialListSize={4}
initialNumToRender={4}
maxToRenderPerBatch={4}
                  removeClippedSubviews={true}
                  keyExtractor={item => item.title} 
                  renderItem={this._renderItem.bind(this)}
                      />
                </View>
              );
            }
  };
}
function DevelopmentModeNotice() {
  if (__DEV__) {
    const learnMoreButton = (
      <Text onPress={handleLearnMorePress} style={styles.helpLinkText}>
        Learn more
      </Text>
    );

    return (
      <Text style={styles.developmentModeText}>
        Development mode is enabled: your app will be slower but you can use
        useful development tools. {learnMoreButton}
      </Text>
    );
  } else {
    return (
      <Text style={styles.developmentModeText}>
        You are not in development mode: your app will run at full speed.
      </Text>
    );
  }
}

